.detail-info {
    position: absolute;
    top: 120px;
    right: 0;
    bottom: 0;
    left: 0;
    width: 90%;
    height: 65%;
    margin: 0 auto;
    background-color: #fff;
    opacity: 0;
    visibility: hidden;
    z-index: 19;
}

.detail-info-fade {
    opacity: 1;
    visibility: visible;
}

.detail-body {
    padding: 50px;
    font-size: 22px;
}