.navbar {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 100000;
}

.navbar .nav-list {
    display: flex;
    padding: 0;
    list-style-type: none;
    margin: 30px;
}

.navbar .nav-list .nav-item {
    font-size: 27px;
    padding: 15px 30px;
    margin-top: 10px;
    margin-left: 20px;
    margin-right: 10px;
    font-weight: 700;
    color: #fff;
    margin-bottom: 5px;
    cursor: pointer;
    animation: visibility 4.5s ease;
}
.navbar .nav-list .nav-item:hover {
    text-decoration: underline;
}

.navbar .nav-list .effect {
    border-radius: 50px;
    margin-left: 150px;
    background-color: #fff;
    color: #8517bd;
    text-decoration: none;
}

.navbar .nav-list .effect:hover {
    text-decoration: none;
}