.fixed-section {
    width: 100%;
    height: 100vh;
    position: relative;
    margin: 0 auto;
    padding: 0;
    overflow: hidden;
}

.fixed-section .detail {
    position: absolute;
    left: 350px;
    bottom: 120px;
    border-radius: 50px;
    padding: 10px 25px;
    font-size: 33px;
    background-color: rgba(0, 0, 0, 0.0);
    color:#fff;
    border: 2px solid #fff;
    cursor: pointer;
    animation: visibility 4.5s ease;
    z-index: 1000;
}


.fixed-section .detail:hover {
    background-color: #fff;
    color: #8517bd;
}

.repeats {
    height: 100vh;
    width: 100%;
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    background-repeat: no-repeat;
    background-size: 1500px 900px;
}
.backgroung {
    top: 0;
    background-image: url("../images/background.png");
    background-size: 1800px 1100px;
    z-index: 10;
}

.backgroung-fade {
    animation: sky 4s ease;
    background-size: 2500px 1700px;
}
.clouds {
    top: 0;
    background-image: url("../images/clouds.png");
    background-position: top right;
    background-size: 2200px 1000px;
    animation: clouds 4s ease;
    z-index: 11;
}
.moon {
    top: -100px;
    z-index: 12;
    background-image: url("../images/moon.png");
    background-size: 1900px;
    background-position: -5px 40px;
}
.moon-ani {
    animation-delay: 2s;
    animation: moon 4s ease;
}
.moon-fade {
    background-position: 1500px -4000px;
    animation: moon-fades 6s ease;
}
.scale-parent-ani{
    animation: scale-parent 3s ease;
    transform: scale(0.7);
    transform-origin: 1800px 600px;
    transition: 1s ease;   
}
.scales {
    position: relative;
    transform: translate(-10px, -20px);
    z-index: 222;
}
.scale {
    transform: perspective(3000px);
    animation: scales 6s ease;
    transform: scale(0.5);
    transform-origin: 600px 1250px;
}

.mosque {
    left: 120px;
    top: 80px;
    animation: mosque 4s ease;
    z-index: 141;
}
.mosque img {
    width: 1700px;
    height: 900px;
}

.little-towers {
    left: 150px;
    right: 0px;
    top: 100px;
    animation: towers 4s ease;
    z-index: 15;

}
.little-towers img {
    width: 1700px;
    height: 900px;
}
.towers {
    top: 100px;
    left: 120px;
    animation: towers 4s ease;
    z-index: 26;
}
.towers-more{
    animation: towers-more 6s ease;
    z-index: 26;
}
.towers img {
    width: 1700px;
    height: 900px;
}
.way {
    height: 800px;
    bottom: 0;
    z-index: 17;

}
.way img {
    width: 150%;
    height: 800px;
}
.way-fade {
    animation: way 6s ease;
    transform: translate(-10px, 1000px);
}


@keyframes visibility {
    0% {
        opacity: 0;
    }
    99% {
        opacity: 0;
    }
    100% {
        opacity: 1; 
    }
}

@keyframes mosque{
    from {
        top: 800px;
    }
    to {
        top: 80px;
    }
}

@keyframes towers{
    from {
        top: 1100px;
    }
    to {
        top: 100px;
    }
  }

@keyframes moon{
    from {
        top: -1000px;
    }
    to {
        top: -100px;
    }
  }

@keyframes clouds{
    from {
        top: -1000px;
    }
    to {
        top: 0px;
    }
  }

@keyframes towers-more {
    from {
    }
    to {
    }
  }

@keyframes moon-fades {
    0% {
        background-position: -5px 40px;
    }
    100% {
        background-position: 900px -750px;
    }
  }

@keyframes scale-parent {
    0% {
        transform: scale(1);
    }
    100% {
        transform: scale(0.7);
        transform-origin: 1800px 600px;
    }
  }

@keyframes mosque-fades {
    0% {
        background-position: 120px 150px;
    }
    100% {
        background-position: 900px -750px;
    }
  }

@keyframes scales {
    0% {
        transform: translate(-10px, -20px);
    }
    50% {
        transform: translate(-200px, 90px);
    }
    75% {
        transform: translate(-200px, 300px);
    }
    80% {
        transform: scale(0.5);
        transform-origin: 600px 1250px;
    }
    100% {
        transform: scale(0.5);
        transform-origin: 600px 1250px;
    }
  }

@keyframes way {
    0% {
        transform: translate(-10px, 10px);
    }
    50% {
        transform: translate(-10px, 100px);
    }
    75% {
        transform: translate(-10px, 200px);
    }
    100% {
        transform: translate(-10px, 1000px);
    }
  }

@keyframes sky {
    0% {
        background-size: 1800px 1100px;
    }
    80% {
        background-size: 2200px 1500px;
    }
    100% {
        background-size: 2500px 1700px;
    }
  }