::-webkit-scrollbar {
    width: 7px;
}
::-webkit-scrollbar-track {
    background: #5114e0; 
    border-radius: 10px;
}
::-webkit-scrollbar-thumb {
    background: #302941; 
    width: 20px;
}
.section-about {
    user-select: none;
    height: 600px;
    width: 700px;
    position: absolute;
    top: 100px;
    left: 100px;
    z-index: 200;
    padding: 20px;
    background-color: #fff;
    opacity: 0;
    border-radius: 50px;
    visibility: hidden;
}
.section-about-ani {
    animation: about 3s ease;
    opacity: 1;
    visibility: visible;
}
.section-about .section-body {
    height: 500px;
    overflow: auto;
    padding: 50px 30px;
}

@keyframes about {
    0% {
        opacity: 0;
        visibility: hidden;
    }
    100% {
        opacity: 1;
        visibility: visible;
    }
  }
